import React, { useEffect, useState } from 'react'
import { useHistory, Link } from 'react-router-dom'
import { getOneCard, deleteCard } from '../../../services/cardsService'
import { parseDate } from '../../../utils/dateParser'
import { notify } from '../../../reducers/notifications'
import { connect } from 'react-redux'

import okIcon from '../../../assets/imgs/ok.svg'
import errorIcon from '../../../assets/imgs/error.svg'

import { Button, ViewCardStyled, InactiveCard, ActiveCard, DeleteButton } from './CardStyled'

const ViewCard = ({id, notify}) => {

    let history = useHistory()

    const closeUserInfo = (e) => {
        e.stopPropagation()
        history.push('/panel/cards')
    }

    const [ card, setCard ] = useState(null)

    useEffect(() => {
        getOneCard(id)
        .then(res => {
            setCard(res)
            console.log(res)
        })
        .catch(err => { 
            console.log(err)
        })
    }, [])

    const removeCard = (id) => {
        console.log(id)
        deleteCard(+id)
        .then(res => {
            notify({
                type: 'success',
                heading: ' ',
                time: 2000
            })
            history.push('/panel/cards?type=inactive')
        })
        .catch(err => {
            notify({
                type: 'error',
                heading: '-   ',
                time: 2000
            })
        })
    }

    return (
        <ViewCardStyled onClick={closeUserInfo}>
            <div className="content" onClick={(e) => e.stopPropagation()}>
                {card ? (

                   card.cardStatus === 0 ? 
                   (
                    <ActiveCard>
                        <h4> </h4>
                        <div className="card-number">
                            <p className="info"> :</p>
                            <div className="number">{card.cardNumber}</div>
                        </div>

                        <div className="card-holder">
                            <p className="info"> :</p>
                            <div className="holder">{card.clientId}</div>
                        </div>

                        <h4>  :</h4>
                        <div className="col-2">
                            <div className="car-number-container">
                                <p className="info"> :</p>
                                <div className="car-number">{card.carNumber}</div>
                            </div>
                            <div className="car-vin-container">
                                <p className="info">VIN : </p>
                                <div className="car-vin">
                                    <p>{card.vin}</p>
                                </div>
                            </div>
                        </div>

                        <div className="col-2 car-make-color">
                            <div className="car-make-container">
                                <p className="info">:</p>
                                <div className="car-make">{card.carMake}</div>
                            </div>
                            <div className="car-color-container">
                                <p className="info">:</p>
                                <div className="car-color">
                                    <p>{card.color} </p><div style={{backgroundColor: card.color}} className="color-box"></div>
                                </div>
                            </div>
                        </div>
                        <h4>  : </h4>
                        <div className="col-2 city-phone">
                            <div className="city-container">
                                <p className="info">: </p>
                                <div className="city">{card.city}</div>
                            </div>
                            <div className="phone-container">
                                <p className="info"> : </p>
                                <div className="phone">{card.phoneNumber}</div>
                            </div>
                        </div>
                        <div className="email-container">
                            <p className="info">Email: </p>
                            <div className="email"><a taget="_blank" href={`mailto:${card.clientId}`}>{card.clientId}</a></div>
                        </div>

                        <h4>  : </h4>
                        <div style={{marginBottom: '20px'}} className=" col-2">
                            <div className="date-activated">
                                <p className="info"> : </p>
                                <div>{parseDate(card.actionStartDate)}</div>
                            </div>
                            <div className="date-sold">
                                <p className="info"> : </p>
                                <div>{parseDate(card.dateOfContractWithClient)}</div>
                            </div>
                        </div>

                        <div className=" col-2">
                            <div className="paid">
                                <p className="info"> : </p>
                                <div>{card.paid ? (<>  <img src={okIcon}/></>) : (<>  <img src={errorIcon}/></>)}</div>
                            </div>
                            <div className="price">
                                <p className="info"> : </p>
                                <div>{card.cardCost}</div>
                            </div>
                        </div>

                        <Button style={{marginTop: '20px'}} invert></Button>
                        <DeleteButton onClick={() => removeCard(card.id)}></DeleteButton>
               
                    </ActiveCard>
                   ) 
                   : 
                   (
                    <InactiveCard>
                        <h4>  </h4>
                        <div className="card-number">
                            <p className="info"> :</p>
                            <div className="number">{card.cardNumber}</div>
                        </div>
                        <div className="service-list">
                            <p className="info"> :</p>
                            <div className="service">{card.ServicesId}</div>
                        </div>

                        <Link to={`/panel/cards/activate/${id}`}><Button></Button></Link>
                        <DeleteButton onClick={() => removeCard(card.id)}></DeleteButton>

                    </InactiveCard>
                   )

                ) : <p>Loading...</p>}

            </div>
        </ViewCardStyled>
    )
}

export default connect(null, { notify })(ViewCard)