import React, { useState, useRef } from 'react'
import styled from 'styled-components'
import { notify } from '../../reducers/notifications'
import { connect } from 'react-redux'
import { useHistory } from 'react-router-dom'
import { putCard, getOneCard } from '../../services/cardsService'


const SelectStyled = styled.ul`
    position: absolute;
    background-color: white;
    box-shadow: 0px 0px 4px 0px rgba(0,0,0,.4);
    border-radius: 10px;
    cursor: pointer;
    top: 0px;
    left: 0;
    z-index: 99;
    width: 100%;

    li {
        list-style: none;
        padding: 5px 0;
        text-align: center;

        &:first-child {
            border-top-left-radius: 10px;
            border-top-right-radius: 10px;
            &:hover {
                background-color: white;
                cursor: default;
            }
        }

        &:last-child {
            border-bottom-left-radius: 10px;
            border-bottom-right-radius: 10px;
        }

        &:hover {
            background-color: lightgrey;
        }

    }
`

const Selected = styled.div`
    cursor: pointer;
    font-size: .8em;
    border-radius: 30px;
    max-width: 110px;
    padding: 3px;
    text-align: center;
    font-weight: bold;
    color: white;
    margin: 0 auto;
    min-width: 110px;
    background-color: #4caf50;

    &.active {
        background-color: #a7a7a7;
    }
`


const StatusSelect = (props) => {

    const [ active, setActive ] = useState(false)

    const node = useRef()

    const handleClickOutside = e => {
        if (node.current.contains(e.target)) {
          // inside click
          return
        }
        setActive(false)
      };

    const openDropDown = (e) => {
        setActive(!active)
    }

    React.useEffect(() => {
        if (active) {
          document.addEventListener("mousedown", handleClickOutside);
        } else {
          document.removeEventListener("mousedown", handleClickOutside);
        }

        return () => {
          document.removeEventListener("mousedown", handleClickOutside);
        };
      }, [active]);

      let history = useHistory()

      const deactivateCard = async (id) => {
            const cardData = await getOneCard(id)
            console.log({
                ...cardData,
                cardStatus: 1
            })
            putCard(id, {
                ...cardData,
                cardStatus: 1
            }).then( res => {
                notify({
                    type: 'success',
                    time: '2000',
                    heading: ' '
                })
            }).catch(err => {
                notify({
                    type: 'error',
                    time: 2000,
                    heading: '  '
                })
            })
      }

      const activateCard = (id) => {
            history.push(`/panel/cards/activate/${id}`)
      }


    if(props.status === 1) {
        return (
            <>
                <Selected className={props.status === 1 ? 'active' : ''} onClick={() => setActive(true)}> </Selected>
                {active ? (
                    <SelectStyled ref={node} onMouseLeave={openDropDown} onClick={(e) => openDropDown(e)}>
                        <li> </li>
                        <li onClick={() => activateCard(props.id)}></li>
                    </SelectStyled>
                ) : null}
            </>
        )
    } else if(props.status === 0) {
        return (
            <>
                <Selected className={props.status === 1 ? 'active' : ''}  onClick={() => setActive(true)}></Selected>
                {active ? (
                    <SelectStyled ref={node} onMouseLeave={openDropDown} onClick={(e) => openDropDown(e)}>
                        <li ></li>
                        <li onClick={() => deactivateCard(props.id)}></li>
                    </SelectStyled>
                ) : null}
            </>
        )
    }

}

export default connect(null, { notify })(StatusSelect)