import React, { useEffect } from 'react'
import { Link, Route, Switch} from 'react-router-dom'
import styled from 'styled-components'
//import StatusSelect from './StatusSelect'
import { getAllOrders } from "../../services/ordersService"
import Loader from './Loader'
import { useLocation } from 'react-router-dom'
import ViewOrder from './ViewOrder/ViewOrder'
import { useQueryState } from "react-router-use-location-state"
//import ActivateCard from './ActivateCard/ActivateCard'
import AddOrder from './AddOrder/AddOrder'

import Card from '../../elements/Card'
import InnerBar from './InnerBar/InnerBar'

const CardCustom = styled(Card)`
    padding: 10px 0;
`

const CardsStyled = styled.div`
    table {
        border-collapse: collapse;
        width: 100%;
        overflow: scroll;
        .center {
            text-align: center;
        }
        thead {
            th {
                text-align: center;
                padding: 10px 5px;
            }
        }
        
        .row_data {
            td {
                padding: 10px 10px;
                font-size: .9em;
                text-align: center;
                &.status {
                    position: relative;
                }

                span {
                    text-align: left;
                }
            }
            &:nth-child(2n) {
                background-color: #ececec;
            }
        }
    }
`


const statusOptions = [
    { value: 1, label: ''},
    { value: 0, label: ''}
]

const Orders = (props) => {
    
    let location = useLocation();


    const [ orders, setOrders ] = React.useState(null)
    console.log(orders)

    useEffect(() => {
        getAllOrders()
        .then(res => {
            setOrders(res)
        })
        .catch(err => {
            console.log(err)
        })
    }, [])


    const [ searchOption, setSearchOption ] = useQueryState('searchOption', 'surname')
    const [ searchValue, setSearchValue ] = useQueryState('searchValue', '')

    useEffect(() => {

    }, [searchOption, searchValue])

        return (
            <CardsStyled >
                <CardCustom style={!orders ?  { minHeight: '80vh' }  : null}>
                <InnerBar searchValue={searchValue} setSearchValue={setSearchValue} setSearchOption={setSearchOption} searchOption={searchOption} loading={orders}/>
            {orders ? (
                    <table>
                        <thead>
                            <tr>
                                <th></th>
                                <th></th>
                                <th className="center">   </th>
                                <th className="center"> </th>
                                <th></th>
                            </tr>
                        </thead>

                        <tbody>
                            {orders.map(order => (
                               <tr className="row_data" key={order.id}>
                                    <td> <Link to={`/panel/orders/${order.id}`}>{order.number}</Link></td>
                                    <td>{order.client}</td>
                                    <td>{order.price}</td>
                                    <td>{order.dateCreated}</td>
                                    <td>{order.rating}</td>
                                </tr>
                            ))}
                        </tbody>
                </table> ) :  <Loader/>}

                </CardCustom>


               <Switch>
                    <Route exact path="/panel/orders/add_order" render={() => <AddOrder />}/>
                    <Route exact path={`/panel/orders/:id`} render={({match}) => <ViewOrder id={match.params.id}/>}/>
                </Switch>  

              
            </CardsStyled>
        )
}



export default Orders