import React, { useState } from 'react'
import styled from 'styled-components'
import { useHistory } from 'react-router-dom'
import { Formik, Form, Field, ErrorMessage } from 'formik'
import Button from '../../../elements/Button'
import { connect } from 'react-redux'
import { notify, clearNotifications } from '../../../reducers/notifications'
import Loader from 'react-loader-spinner'
import closeIcon from '../../../assets/imgs/close_icon.svg'
import { addOrder } from '../../../services/ordersService'

import { AddOrderStyled, Input, TwoInLine, FormStyled, FieldGroup, SubmitButton } from './AddOrderStyled'


const AddOrder = ({notify, clearNotifications}) => {

    let history = useHistory()

    const closeUserInfo = (e) => {
        e.stopPropagation()
        history.push('/panel/orders')
    }

    const confirmation = (e) => {
        clearNotifications()
        notify({
            heading: ' ?',
            text: '  ',
            type: 'submit',
            onOkFunc: () => closeUserInfo(e)
        })
    }

    const [ loading, setLoading ] = useState(false)

    const onSubmit = (values) => {
        console.log(values)

        setLoading(true)
        addOrder(values)
        .then(res => {
            setLoading(false)
            notify({
                type: 'success',
                time: 2000,
                heading: ' ',
                text: ' '
            })
        })
        .catch(err => {
            setLoading(false)
            notify({
                type: 'error',
                time: 2000,
                heading: '-   '
            })
        })
    }

    const initialValues = {
        ClientId: 0,
        TechnicalAssistanceCardNumber: '',
        Location: '',
        PointOfDeparture: '',
        PointOfDestination: '',
        ServiceId: 0,
        ServicesCostForClient: 0,
        CountryId: 0,
        ServicesCostForContractor: 0,
        ServiceTime: '',
        PaymentMethodId: 0,
        ServiceRating: 0,
        OperatorComment: '',
        ClientReview: ''
    }

    return (
        <AddOrderStyled>
            <div className="content" onClick={(e) => e.stopPropagation()}>
                <div className="card_title"><span>  : </span><img onClick={confirmation} src={closeIcon}/></div>
                <Formik initialValues={initialValues} onSubmit={onSubmit}>
                    <FormStyled>

                        <TwoInLine>
                            <FieldGroup>
                                <label> </label>
                                <Input name="PointOfDeparture" type="text" placeholder=" "/>
                            </FieldGroup>
                            <FieldGroup>
                                <label> </label>
                                <Input name="PointOfDestination" type="text" placeholder=" "/>
                            </FieldGroup>
                        </TwoInLine>

                        <FieldGroup>
                            <label> </label>
                            <Input name="TechnicalAssistanceCardNumber" type="number" placeholder=" "/>
                        </FieldGroup>

                        <FieldGroup>
                            <label></label>
                            <Input name="Location" type="text" placeholder=""/>
                        </FieldGroup>


                        <TwoInLine>
                            <FieldGroup>
                                <label>  </label>
                                <Input name="ServicesCostForContractor" type="number" placeholder="  "/>
                            </FieldGroup>
                            <FieldGroup>
                                <label>  </label>
                                <Input name="ServicesCostForClient" type="number" placeholder="  "/>
                            </FieldGroup>
                        </TwoInLine>

                        <TwoInLine>
                            <FieldGroup>
                                <label> </label>
                                <Input name="ClientReview" type="text" placeholder=" "/>
                            </FieldGroup>
                            <FieldGroup>
                                <label> </label>
                                <Input name="ServiceRating" max="10" type="number" placeholder=" "/>
                            </FieldGroup>
                        </TwoInLine>

                        <FieldGroup>
                            <label> </label>
                            <Input name="OperatorComment" max="10" type="text" placeholder=" "/>
                        </FieldGroup>

                        <SubmitButton invert type="submit">{loading ? <Loader type="Puff" color="#fff" height={15} width={15}/> : " "}</SubmitButton>
                    </FormStyled>
                </Formik>
            </div>
        </AddOrderStyled>
    )
}

export default connect(null, { notify, clearNotifications })(AddOrder)