import React, { useState } from 'react'
import { Formik, Form, Field, ErrorMessage } from 'formik'
import Loader from 'react-loader-spinner'
import { notify } from '../../../reducers/notifications'
import { connect } from 'react-redux'
import { useHistory } from 'react-router-dom'
import { activateCard } from '../../../services/cardsService'
import { ActivateCardStyled, SubmitButton, Input, TwoInLine, FormStyled, FieldGroup } from './ActivateCardStyled'


const ActivateCard = ({id, notify}) => {


    const initialValues = {
        "Id": +id,
        "ClientId": 0,
        "ClientInfo": {
            "Id": 0,
            "ClientFristName": "",
            "ClientSecondName": "",
            "ClientThirdName": "",
            "Email": "",
            "PhoneNumber": "",
            "CarBrandId": 0,
            "CarBrand": {
            "Id": 0,
            "Name": ""
            },
            "StateNumber": "",
            "Region": ""
        },
        "SellerFirstName": "",
        "SellerSecondName": "",
        "SellerThirdName": "",
        "ManagerFirstName": "",
        "ManagerSecondName": "",
        "ManagerThirdName": "",
        "EmployerFirstName": "",
        "EmployerSecondName": "",
        "EmployerThirdName": ""
    }

    const [ loading, setLoading ] = useState(false)

    let history = useHistory()

    const onSubmit = (values) => {
        setLoading(true)

        console.log(id, values)

        activateCard(id, values)
        .then(res => {
            setLoading(false)
            notify({
                type: 'success',
                time: 2000,
                heading: ' ',
                text: ' '
            })
            history.push('/panel/cards')
        })
        .catch(err => {
            notify({
                type: 'error',
                time: 2000,
                heading: '-   '
            })
            setLoading(false)
        })
        
    }


    return (
        <ActivateCardStyled>
            <h2 style={{marginBottom: '20px'}}> </h2>

            <Formik initialValues={initialValues} onSubmit={onSubmit}>
                <FormStyled>

                    <TwoInLine>
                        <FieldGroup>
                            <label> </label>
                            <Input name="ClientInfo.ClientFristName" type="text" placeholder=" "/>
                        </FieldGroup>

                        <FieldGroup>
                            <label> </label>
                            <Input name="ClientInfo.ClientSecondName" type="text" placeholder=" "/>
                        </FieldGroup>
                    </TwoInLine>

                    <FieldGroup>
                        <label> </label>
                        <Input name="ClientInfo.ClientThirdName" type="text" placeholder=" "/>
                    </FieldGroup>

                    <FieldGroup>
                        <label>Email </label>
                        <Input name="ClientInfo.Email" type="email" placeholder="Email "/>
                    </FieldGroup>

                    <FieldGroup>
                        <label> </label>
                        <Input name="ClientInfo.PhoneNumber" type="tel" placeholder=" "/>
                    </FieldGroup>

                    <FieldGroup>
                        <label>  </label>
                        <Input name="ClientInfo.CarBrand.Name" type="text" placeholder="  "/>
                    </FieldGroup>

                    <FieldGroup>
                        <label> </label>
                        <Input name="ClientInfo.Region" type="text" placeholder=" "/>
                    </FieldGroup>

                    <FieldGroup>
                        <label> </label>
                        <Input name="ClientInfo.StateNumber" type="text" placeholder=" "/>
                    </FieldGroup>


                    <TwoInLine>
                        <FieldGroup>
                            <label> </label>
                            <Input name="SellerFirstName" type="text" placeholder=" "/>
                        </FieldGroup>

                        <FieldGroup>
                            <label> </label>
                            <Input name="SellerSecondName" type="text" placeholder=" "/>
                        </FieldGroup>                        
                    </TwoInLine>

                    <FieldGroup>
                            <label> </label>
                            <Input name="SellerThirdName" type="text" placeholder=" "/>
                    </FieldGroup>  


                    <TwoInLine>
                        <FieldGroup>
                            <label> </label>
                            <Input name="ManagerFirstName" type="text" placeholder=" "/>
                        </FieldGroup>

                        <FieldGroup>
                            <label> </label>
                            <Input name="ManagerSecondName" type="text" placeholder=" "/>
                        </FieldGroup>                        
                    </TwoInLine>

                    <FieldGroup>
                        <label> </label>
                        <Input name="ManagerThirdName" type="text" placeholder=" "/>
                    </FieldGroup>  

                    

                    <TwoInLine>
                        <FieldGroup>
                            <label> </label>
                            <Input name="EmployerFirstName" type="text" placeholder=" "/>
                        </FieldGroup>
                        <FieldGroup>
                            <label> </label>
                            <Input name="EmployerSecondName" type="text" placeholder=" "/>
                        </FieldGroup>
                    </TwoInLine>
                    
                    <FieldGroup>
                        <label> </label>
                        <Input name="EmployerThirdName" type="text" placeholder=" "/>
                    </FieldGroup>

                    <SubmitButton className="submit_btn" type="submit">{loading ? <Loader type="Puff" color="#000" height={15} width={15}/> : ""}</SubmitButton>
                </FormStyled>
            </Formik>

        </ActivateCardStyled>
    )
}

export default connect(null, { notify })(ActivateCard)