import axios from "axios"


//const baseUrl = '/api/cards'
const baseUrl = 'https://cors-anywhere.herokuapp.com/https://kadet-assistance.herokuapp.com/api/Orders'
 
export const getAllOrders = async () => {
    const res = await axios.get(`${baseUrl}/GetOrders`)
    return res.data
}

export const addOrder = async (data) => {
    const res = await axios.post(`${baseUrl}/PostOrder`, data)
    return res.data
}
