const SubmitButton = styled(Button)`
    margin-top: 25px;
`


const AddCard = (props) => {


    const closeUserInfo = (e) => {
        e.stopPropagation()
        return props.history.push('/panel/cards')
    }

    const confirmation = (e) => {
        props.clearNotifications()
        props.notify({
            heading: ' ?',
            text: '  ',
            type: 'submit',
            onOkFunc: () => closeUserInfo(e)
        })
    }

    const [ selectedStatus, setSelectedStatus ] = useState('active')
    const [ selectedPosition, setSelectedPosition ] = useState(0)

    const initialValues = {

        "CardNumber": "",
        "CardCost": 0,
        "Duration": "2020-05-06T21:21:10.476Z",
        "VIN": "3456jk2234",
        "Color": "blue",
        "ServicesId": 1,
        "NumberOfContractWithKadet": 12,
        "DateOfContractWithKadet": "2020-05-06T21:21:10.476Z",
        "NumberOfContractWithClient": 14,
        "DateOfContractWithClient": "2020-05-06T21:21:10.476Z",
        "ActionStartDate": "2020-05-06T21:21:10.476Z",
        "DeactivationDate": "2020-05-06T21:21:10.476Z",
        "CardAppeal": "akjf"

    }

    const [ loading, setLoading ] = useState(false)

    let history = useHistory()

    const onSubmit = (values, { setSubmitting }) => {

        setLoading(true)
        console.log(values)
        addCard(values)
        .then(res => {
           console.log(res)

           setLoading(false)

           history.push(`/panel/cards?type=inactive`)

            props.notify({
                type: 'success',
                heading: ' !',
                time: 2000
            })
         })
         .catch(err => {

            setLoading(false)
            props.notify({
                type: 'error',
                heading: '-   !',
                time: 2000
            })
            console.log(err)
         })
        

    }


    const statusOptions = [
        { value: 'fired', label: ''},
        { value: 'day_off', label: ''},
        { value: 'active', label: ''},
        { value: 'vocation', label: ' '},
        { value: 'sick_leave', label: ' '},
    ]

    const servicesOptions = [0, 1, 2]

    return (
        <AddCardStyled>
            <div className="content" onClick={(e) => e.stopPropagation()}>
                <div className="card_title"><span>  : </span><img onClick={confirmation} src={closeIcon}/></div>
                <Formik initialValues={initialValues} onSubmit={onSubmit}>

                    <FormStyled>

                        <div className="form_line full_name">
                            <FieldGroup>
                                <label htmlFor="FirstName">: </label>
                                <InputStyled required placeholder="" name="CardNumber" type="text" />
                            </FieldGroup>
                            <FieldGroup>
                                <label htmlFor="SecondName"> : </label>
                                <InputStyled required placeholder="" name="CardCost" type="number" />
                            </FieldGroup>
                        </div>

                        <FieldGroup>
                            <div className="position">
                                    <label htmlFor="AnotherLanguage"> : </label>
                                    <PositionPicker selected={selectedPosition} setSelected={setSelectedPosition} options={servicesOptions}/>
                            </div>
                        </FieldGroup>
                        <SubmitButton className="submit_btn" type="submit">{loading ? <Loader type="Puff" color="#000" height={15} width={15}/> : ""}</SubmitButton>
                    </FormStyled>
                </Formik>
            </div>
        </AddCardStyled>
    )
}

export default connect(null, {notify, clearNotifications})(withRouter(AddCard))