import React from 'react'
import styled from 'styled-components'
import { Link, useHistory } from 'react-router-dom'

import Filters from './Filters'
import Search from './Search'

//import addIcon from './icons/add.svg'

import ButtonModern from '../../../elements/ButtonModern'

const InnerBarStyled = styled.div`
    padding: 20px 10px;
    display: flex;
    align-items: center;

    .page-info {
        font-size: .9em;
        margin: 0 15px;
    }

    @media screen and (max-width: 600px) {
        flex-wrap: wrap;
        margin: 0;
    }
`

const InnerBar = (props) => {

    let history = useHistory()

    const addOrder = () => {
        history.push('/panel/orders/add_order')
    }

    return (
        <InnerBarStyled>
            <ButtonModern style={{marginRight: 'auto'}} onClick={addOrder}></ButtonModern>
            <Search setSearchValue={props.setSearchValue} searchValue={props.searchValue} setSearchOption={props.setSearchOption} searchOption={props.searchOption}/>
        </InnerBarStyled>
    )
}

export default InnerBar